}
@keyframes run {
  0% {
    transform: translateX(-150px);
  }
  100% {
    transform: translateX(100vw);
  }
}
.animate-run {
  animation: run 8s linear infinite;
}
@keyframes slow-spin {
  0% {
    transform: rotate(0deg);
  }
  5% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.animate-slow-spin {
  animation: slow-spin 5s linear infinite;
